

.hero{
    width: 100vw;
    height: 75vh;
    position: absolute;
    top: 0;
    left: 0;
    //border: 2px solid black;
    background-color: white;
    //background-color: #365747;
    order: 1;


    .hero-container{
        position: absolute;
        width: 90%;
        height: 90%;
        top: 15%;
        left: 5%;
        border-radius: 1rem;
        background: linear-gradient(to top, #0ba360 0%, #3cba92 100%);
    }

    .hero-icon{
        //border: 2px solid red;
        width: 1.2rem;
        height: 1.2rem;
        position: absolute;
        top: 3.5%;
        left: 2%;
      
    }

    .hero-title{
        font-family: 'Work Sans', sans-serif;
        font-size: 1.2rem;
        color:#0ba360;
        width: 12rem;
        height: 1rem;
        cursor: pointer;
        position: absolute;
        top: 1%;
        left: 4%;
        
    }

    .form{
        width: 25%;
        height: 2rem;
        position: absolute;
        border: 1.4px solid#0ba360;
        border-radius: 1rem;
        padding: 0.2rem;
        top: 3%;
        right: 5%;

        .search{
            width: 80%;
            height: 100%;
            padding: 0 0 0 10%;
            border-radius: 0.3rem;
            border: none;
            outline: none;
            cursor: text;

        }

    }

    .search-icon{
        width: 2rem;
        height: 2rem;
        position: absolute;
        background-color: inherit;
        border: none;
        outline: none;
        
        top: 5%;
        left: 2%;
        
        &:hover{
            opacity: 0.7;
            cursor: pointer;
        }
        .magnify-icon{
            width: 1.2rem;
            height: 1.2rem;
            color: #459866;
        }
    }

    h1{
        font-family: 'Work Sans', sans-serif;
        width: 30%;
        height: 4.5rem;
        font-size: 4rem;
        color: white;
        position: absolute;
        top: 15%;
        left: 10%;
    }

    h3{
        font-family: 'Work Sans', sans-serif;
        width: 50%;
        height: 2rem;
        font-size: 1.2rem;
        color: white;
        opacity: 0.6;
        position: absolute;
        top: 60%;
        left: 10%;
    }

    .find-btn{
        width: 10rem;
        height: 3rem;
        font-family: 'Work Sans', sans-serif;
        color: #FFFFFF;
        font-weight: 500;
        //background-color: #459866;
       background-color: #3a7253;
        position: absolute;
        top: 70%;
        left: 10%;
        border-radius: 0.3rem;
        border: none;
        outline: none;

        &:hover{
            cursor: pointer;
            background-color: #FFFFFF;
            border: 2px solid #3a7253;
            color: #3a7253;
        }
    }
  
    .hero-img{
        width: 30%;
        height: 60%;
        position: absolute;
        top: 15%;
        right: 10%;
    }
   
        
}

@media screen and (max-width: 389px) {
    .hero{
        height: 100vh;
        border: 2px solid red;

        .hero-title{
            left: 10%;
        }

        .form{
            top: 2%;
            width: 50%;

        }
        .search-icon{
            left: 72%;
        }
        h1{
            font-size: 2rem;
        }

        h3{
            top: 65%;
            font-size: 1rem;
        }

        .find-btn{
            width: 10rem;
            top: 85%;
        }

        .hero-img {
            width: 50%;
            height: 30%;
            top: 24%;
            left: 45%;
        }
    }
}

@media screen and (min-width: 390px) and (max-width: 600px) {
    .hero{
        height: 100vh;
        border: 2px solid blue;

        .hero-title{
            left: 10%;
         
        }


        .form{
            top: 2%;
            width: 50%;
        }

        .search-icon{
            left: 72%;
        }
       
        h1{
            font-size: 3rem;
        }

        h3{
            top: 75%;
        }

        .find-btn{
            top: 90%;

        }

        .hero-img {
            width: 50%;
            height: 30%;
            top: 33%;
            left: 45%;
        }
    
    }
}

@media screen and (max-width: 820px) and (min-width: 610px){
    .hero{
       height: 75vh;
        .form{
            width: 50%;
            height: 3rem;
        }

        .search-icon{
            top: 15%;
        }

        h1{
            font-size: 4rem;
            top: 5%;
        }
        h3{
            top: 60%;
        }

        .find-btn{
            width: 12rem;
            height: 4rem;
            top: 80%;
        }
    }
}

